const Card = styled.div`
  width: 275px;
  height: 427px;
  background: #FFFFFF;
  box-shadow: 0px 0px 30px rgba(51, 1, 109, 0.1);
  border-radius: 8px;
  border-bottom: 10px solid ${props => props.color};
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-content: center;
  background-color: ${props => props.bg};
  transition: 0.2s;
`

const MockCard = styled.div`
  background: green;
  width: 275px;
  height: 427px;
`

const Reasons = () => {
  const [index, setIndex] = useState(0);
  const [direction, setDirection] = useState(null);

  const handleSelect = (selectedIndex, e) => {
    setIndex(selectedIndex);
    setDirection(e.direction);
  }

  return (
    <>
      <div className='row my-5'>

        <SectionTitle>
          <h2><span className='text-purple'>Четири причини</span> да се качиш на NetSurf-а</h2>
          <hr className='center' />

        </SectionTitle>
        <div className='row w-100 d-flex justify-content-center align-items-center'>
            {/* <Slider {...settings}> */}
            {reasonsData.reasons.map((r, index) => (
              <div className='col-12 col-md-6 col-xl-3'>
                <div className='ml-2'>
                  <CardContent
                    title={r.title}
                    content={r.content}
                    image={r.image}
                    color={r.color}
                  />
                </div>
              </div>
            ))}
            {/* </Slider> */}
        </div>
      </div>
    </>
  );
}


const CardContent = ({ title, image, content, color }) => {
  const [isOpen, setOpen] = useState(false);

  if (isOpen) {
    return (
      <Card className='px-1 py-2 text-left card-open' bg={color} color={color} onClick={(_) => setOpen(!isOpen)}>
        <p>{content}</p>
        <div className='text-center'>
          <span className='icon reason-close'></span>
        </div>
      </Card>
    )
  } else {
    return (
      <Card className='px-2 py-3 text-center' bg={"white"} color={color} onClick={(_) => setOpen(!isOpen)}>
        <h3>{title}</h3>
        <img src={image} alt='Причина 1' />
        <div>
          <span className='icon reason-open'></span>
        </div>
      </Card>
    );
  }

}

const settings = {
  dots: true
}


export default Reasons;